<template>
  <div class="main">
    <nav class="c-breadcrumb-bg" aria-label="breadcrumb">
      <div class="container">
        <ol class="breadcrumb">
          <li class="breadcrumb-item"><router-link :to="{ name: 'Shop'}" >首頁</router-link></li>
          <li class="breadcrumb-item active" aria-current="page">經典日劇</li>
        </ol>
      </div>
    </nav>
    <main class="container pb-5">
      <div class="row">
        <CardTV v-for="item in filterData[pagination.current_page - 1 ]" :key="item.id" :item= item />
        <div class="w-100"></div>
        <Pagination :pages="pagination" @emitPages="editPage" class="m-3"></Pagination>
      </div>
    </main>
  </div>
</template>

<script>
import CardTV from '@/components/CardTV.vue';
import Pagination from '@/components/Pagination.vue';
import { mapGetters } from 'vuex';

export default {
  name: 'JapanTv',
  components: {
    CardTV,
    Pagination,
  },
  data() {
    return {
      pagination: {
        current_page: 1,
        total_pages: 0,
      },
    };
  },
  created() {
    this.$store.dispatch('getAllProducts');
  },
  computed: {
    ...mapGetters(['japanTv']),
    filterData() {
      const vm = this;
      const newData = [];
      vm.japanTv.forEach((item, i) => {
        if (i % 6 === 0) {
          newData.push([]);
        }
        // eslint-disable-next-line radix
        const page = parseInt(i / 6);
        newData[page].push(item);
      });
      vm.pagination.total_pages = newData.length;
      return newData;
    },
  },
  methods: {
    editPage(page) {
      const vm = this;
      vm.pagination.current_page = page;
      window.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
    },
  },
};
</script>
